<template>
  <div class="furniture-product-view">
    <h1>Просмотр изделия</h1>
    <div v-if="product">
      <h2>{{ product.name }}</h2>
      <p><strong>ID:</strong> {{ product.id }}</p>
      <p><strong>Категория:</strong> {{ product.category }}</p>
      <p><strong>Цена:</strong> {{ product.price }} ₽</p>
      <p><strong>Материал:</strong> {{ product.material }}</p>
      <p><strong>Габариты:</strong> {{ product.dimensions }}</p>
      <p><strong>Описание:</strong> {{ product.description }}</p>
      <p><strong>Наличие:</strong> {{ product.in_stock ? 'В наличии' : 'Нет в наличии' }}</p>
      <img
        v-if="product.image_url"
        :src="getAssetImage(product.image_url)"
        :alt="product.name"
        style="width: 300px; height: auto;"
      />
      <span v-else>Нет изображения</span>
    </div>
    <div v-else>
      <p>Загрузка данных...</p>
    </div>
    <router-link to="/products" class="btn btn-secondary">Назад к списку</router-link>
  </div>
</template>

<script>
import { furnitureApi } from '@/services/api'; // Предполагаем, что такой API есть

export default {
  data() {
    return {
      product: null,
    };
  },
  created() {
    this.fetchProduct();
  },
  methods: {
    fetchProduct() {
      const id = this.$route.params.id;
      furnitureApi.getProductById(id)
        .then(response => {
          this.product = response.data;
        })
        .catch(error => {
          console.error('Ошибка при получении данных о продукте:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
  },
};
</script>

<style scoped>
.furniture-product-view {
  margin: 20px auto;
  max-width: 700px;
}
img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
.btn {
  margin-top: 20px;
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  display: inline-block;
}
.btn:hover {
  background-color: #0056b3;
}
</style>