<template>
  <div id="app">
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <div class="container">
        <router-link to="/" class="navbar-brand">Мебельный цех</router-link>
        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav me-auto">
            <li class="nav-item">
              <router-link to="/" class="nav-link">Главная</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/about" class="nav-link">О нас</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/contacts" class="nav-link">Контакты</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/products" class="nav-link">Товары</router-link>
            </li>
            <li class="nav-item">
              <router-link to="/employees" class="nav-link">Сотрудники</router-link>
            </li>
          </ul>

          <ul class="navbar-nav ms-auto">
            <!-- Если пользователь авторизован -->
            <li v-if="isAuthenticated" class="nav-item d-flex align-items-center">
              <span class="text-light me-2">Привет, {{ username }}</span>
              <button @click="logout" class="btn btn-outline-light btn-sm">Выйти</button>
            </li>
            <!-- Если пользователь не авторизован -->
            <template v-else>
              <li class="nav-item">
                <router-link to="/login" class="nav-link">Войти</router-link>
              </li>
              <li class="nav-item">
                <router-link to="/register" class="nav-link">Регистрация</router-link>
              </li>
            </template>
          </ul>
        </div>
      </div>
    </nav>

    <div class="container mt-4">
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      isAuthenticated: false,
      username: '',
      role: '' // Роль пользователя
    };
  },
  async created() {
    await this.checkAuth();
  },
  methods: {
    async checkAuth() {
      try {
        const response = await fetch('/protected');
        if (response.ok) {
          const data = await response.json();
          this.isAuthenticated = true;
          this.username = data.username; // Имя пользователя
          this.role = data.role; // Роль пользователя
        } else {
          this.isAuthenticated = false;
        }
      } catch (error) {
        console.error('Ошибка при проверке авторизации:', error);
        this.isAuthenticated = false;
      }
    },
    async logout() {
      try {
        await fetch('/logout', { method: 'GET' });
        this.isAuthenticated = false;
        this.username = '';
        this.role = '';
        this.$router.push('/login');
      } catch (error) {
        console.error('Ошибка при выходе:', error);
      }
    }
  }
};
</script>

<style scoped>
.nav-link {
  color: white !important;
  text-decoration: none;
}

.nav-link:hover {
  color: #cccccc !important;
}

.btn-outline-light {
  border-color: #fff;
  color: #fff;
}

.btn-outline-light:hover {
  background-color: #fff;
  color: #000;
}
</style>