<template>
  <div class="furniture-product-create">
    <h1>Создание нового изделия</h1>
    <form @submit.prevent="createProduct">
      <div class="form-group">
        <label for="name">Название:</label>
        <input type="text" id="name" v-model="formData.name" required />
      </div>

      <div class="form-group">
        <label for="category">Категория:</label>
        <input type="text" id="category" v-model="formData.category" required />
      </div>

      <div class="form-group">
        <label for="price">Цена (₽):</label>
        <input type="number" id="price" v-model.number="formData.price" required />
      </div>

      <div class="form-group">
        <label for="description">Описание:</label>
        <textarea id="description" v-model="formData.description"></textarea>
      </div>

      <div class="form-group">
        <label for="material">Материал:</label>
        <input type="text" id="material" v-model="formData.material" required />
      </div>

      <div class="form-group">
        <label for="dimensions">Габариты (см):</label>
        <input type="text" id="dimensions" v-model="formData.dimensions" placeholder="например: 200x90x80" required />
      </div>

      <div class="form-group">
        <label for="image_url">URL изображения:</label>
        <input type="text" id="image_url" v-model="formData.image_url" />
      </div>

      <div class="form-group">
        <label for="in_stock">Наличие:</label>
        <select id="in_stock" v-model="formData.in_stock" required>
          <option :value="true">В наличии</option>
          <option :value="false">Нет в наличии</option>
        </select>
      </div>

      <button type="submit" class="btn btn-success">Создать</button>
      <router-link to="/products" class="btn btn-secondary">Отмена</router-link>
    </form>
  </div>
</template>

<script>
import { furnitureApi } from '@/services/api'; // Предполагаем, что такой API есть

export default {
  data() {
    return {
      formData: {
        name: '',
        category: '',
        price: null,
        description: '',
        material: '',
        dimensions: '',
        image_url: '',
        in_stock: true,
      },
    };
  },
  methods: {
    createProduct() {
      const newProduct = {
        name: this.formData.name.trim(),
        category: this.formData.category.trim(),
        price: parseFloat(this.formData.price),
        description: this.formData.description.trim(),
        material: this.formData.material.trim(),
        dimensions: this.formData.dimensions.trim(),
        image_url: this.formData.image_url.trim(),
        in_stock: this.formData.in_stock,
      };

      if (!newProduct.name || !newProduct.category || !newProduct.price || !newProduct.material || !newProduct.dimensions) {
        alert('Пожалуйста, заполните все обязательные поля.');
        return;
      }

      furnitureApi.addProduct(newProduct)
        .then(() => {
          alert('Изделие успешно создано!');
          this.$router.push({ name: 'ProductsList' }); // Переход к списку товаров
        })
        .catch(error => {
          console.error('Ошибка при создании изделия:', error);
          alert('Произошла ошибка при создании изделия.');
        });
    },
  },
};
</script>

<style scoped>
.furniture-product-create {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input,
textarea,
select {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

textarea {
  resize: vertical;
}

.btn {
  padding: 10px 15px;
  margin-right: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn:hover {
  opacity: 0.9;
}
</style>