<template>
  <div class="furniture-employee-create">
    <h1>Добавить нового сотрудника</h1>
    <form @submit.prevent="createEmployee">
      <div class="form-group">
        <label for="full_name">ФИО:</label>
        <input type="text" id="full_name" v-model="formData.full_name" required />
      </div>

      <div class="form-group">
        <label for="position">Должность:</label>
        <input type="text" id="position" v-model="formData.position" required />
      </div>

      <div class="form-group">
        <label for="phone">Телефон:</label>
        <input type="text" id="phone" v-model="formData.phone" required />
      </div>

      <div class="form-group">
        <label for="email">Email:</label>
        <input type="email" id="email" v-model="formData.email" />
      </div>

      <div class="form-group">
        <label for="hire_date">Дата найма:</label>
        <input type="date" id="hire_date" v-model="formData.hire_date" required />
      </div>

      <div class="form-group">
        <label for="salary">Зарплата (₽):</label>
        <input type="number" id="salary" v-model.number="formData.salary" required />
      </div>

      <div class="form-group">
        <label for="experience_years">Стаж работы (лет):</label>
        <input type="number" id="experience_years" v-model.number="formData.experience_years" required />
      </div>

      <div class="form-group">
        <label for="image_url">URL фото:</label>
        <input type="text" id="image_url" v-model="formData.image_url" />
      </div>

      <button type="submit" class="btn btn-success">Создать</button>
      <router-link to="/employees" class="btn btn-secondary">Отмена</router-link>
    </form>
  </div>
</template>

<script>
import { employeesApi } from '@/services/api'; // Предполагаем, что такой API есть

export default {
  data() {
    return {
      formData: {
        full_name: '',
        position: '',
        phone: '',
        email: '',
        hire_date: '',
        salary: null,
        experience_years: null,
        image_url: '',
      },
    };
  },
  methods: {
    createEmployee() {
      const newEmployee = {
        full_name: this.formData.full_name.trim(),
        position: this.formData.position.trim(),
        phone: this.formData.phone.trim(),
        email: this.formData.email.trim(),
        hire_date: this.formData.hire_date,
        salary: parseFloat(this.formData.salary),
        experience_years: parseInt(this.formData.experience_years),
        image_url: this.formData.image_url.trim(),
      };

      // Проверка обязательных полей
      if (
        !newEmployee.full_name ||
        !newEmployee.position ||
        !newEmployee.phone ||
        !newEmployee.hire_date ||
        !newEmployee.salary ||
        !newEmployee.experience_years
      ) {
        alert('Пожалуйста, заполните все обязательные поля.');
        return;
      }

      employeesApi.addEmployee(newEmployee)
        .then(() => {
          alert('Сотрудник успешно добавлен!');
          this.$router.push({ name: 'EmployeesList' });
        })
        .catch(error => {
          console.error('Ошибка при добавлении сотрудника:', error);
          alert('Произошла ошибка при добавлении сотрудника.');
        });
    },
  },
};
</script>

<style scoped>
.furniture-employee-create {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input,
textarea,
select {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.btn {
  padding: 10px 15px;
  margin-right: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

.btn-secondary {
  background-color: #6c757d;
  color: white;
}

.btn:hover {
  opacity: 0.9;
}
</style>