<template>
  <div class="furniture-products-index">
    <h1>Список мебельных изделий</h1>
    <router-link to="/products/add" class="btn btn-primary">Добавить продукт</router-link>
    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>Название</th>
          <th>Категория</th>
          <th>Цена</th>
          <th>Материал</th>
          <th>Габариты</th>
          <th>В наличии</th>
          <th>Изображение</th>
          <th>Действия</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="product in products" :key="product.id">
          <td>{{ product.id }}</td>
          <td>{{ product.name }}</td>
          <td>{{ product.category }}</td>
          <td>{{ product.price }} ₽</td>
          <td>{{ product.material }}</td>
          <td>{{ product.dimensions }}</td>
          <td>
            <span :class="['availability', product.in_stock ? 'available' : 'unavailable']">
              {{ product.in_stock ? 'В наличии' : 'Нет в наличии' }}
            </span>
          </td>
          <td>
            <img
              v-if="product.image_url"
              :src="getAssetImage(product.image_url)"
              :alt="product.name"
              style="width: 100px; height: auto;"
            />
            <span v-else>Нет изображения</span>
          </td>
          <td>
            <router-link :to="{ name: 'ProductView', params: { id: product.id } }" class="btn btn-info">
              Просмотр
            </router-link>
            <router-link :to="{ name: 'ProductEdit', params: { id: product.id } }" class="btn btn-warning">
              Редактировать
            </router-link>
            <button @click="deleteProduct(product.id)" class="btn btn-danger">Удалить</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Пагинация -->
    <div class="pagination">
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="btn btn-secondary"
      >
        Предыдущая
      </button>
      <span>Страница {{ currentPage }} из {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="btn btn-secondary"
      >
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import { furnitureApi } from '@/services/api'; // Подразумевается, что такой API будет

export default {
  data() {
    return {
      products: [],
      currentPage: 1,
      limit: 10,
      total: 0,
      totalPages: 0,
    };
  },
  created() {
    this.fetchProducts();
  },
  methods: {
    fetchProducts() {
      furnitureApi.getProducts(this.currentPage, this.limit)
        .then(response => {
          console.log('Response from server:', response);
          if (!response.data || !response.data.meta) {
            throw new Error('Invalid response format from server');
          }
          this.products = response.data.data;
          this.total = response.data.meta.total;
          this.totalPages = response.data.meta.totalPages;
        })
        .catch(error => {
          console.error('Ошибка при получении списка товаров:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    changePage(page) {
      if (page < 1 || page > this.totalPages) return;
      this.currentPage = page;
      this.fetchProducts();
    },
    deleteProduct(id) {
      if (!confirm('Вы уверены, что хотите удалить этот продукт?')) return;

      furnitureApi.deleteProduct(id)
        .then(() => {
          this.products = this.products.filter(product => product.id !== id);
          alert('Продукт успешно удален!');
        })
        .catch(error => {
          console.error('Ошибка при удалении продукта:', error);
          alert(`Произошла ошибка при удалении продукта: ${error.message}`);
        });
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg';
      }
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
.table img {
  width: 50px;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.btn {
  margin-right: 5px;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 14px;
}
.pagination span {
  margin: 0 10px;
  font-size: 16px;
}

/* Стили для поля "Наличие" */
.availability {
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 5px;
}
.available {
  color: #fff;
  background-color: green;
}
.unavailable {
  color: #fff;
  background-color: red;
}
</style>