<template>
  <div class="furniture-employees-index">
    <h1>Список сотрудников</h1>
    <router-link to="/employees/add" class="btn btn-primary">Добавить сотрудника</router-link>
    <table class="table">
      <thead>
        <tr>
          <th>#</th>
          <th>ФИО</th>
          <th>Должность</th>
          <th>Телефон</th>
          <th>Email</th>
          <th>Дата найма</th>
          <th>Стаж</th>
          <th>Изображение</th>
          <th>Действия</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="employee in employees" :key="employee.id">
          <td>{{ employee.id }}</td>
          <td>{{ employee.full_name }}</td>
          <td>{{ employee.position }}</td>
          <td>{{ employee.phone }}</td>
          <td>{{ employee.email }}</td>
          <td>{{ formatDate(employee.hire_date) }}</td>
          <td>{{ employee.experience_years }} лет</td>
          <td>
            <img
              v-if="employee.image_url"
              :src="getAssetImage(employee.image_url)"
              :alt="employee.full_name"
              style="width: 100px; height: auto;"
            />
            <span v-else>Нет фото</span>
          </td>
          <td>
            <router-link :to="{ name: 'EmployeeView', params: { id: employee.id } }" class="btn btn-info">
              Просмотр
            </router-link>
            <router-link :to="{ name: 'EmployeeEdit', params: { id: employee.id } }" class="btn btn-warning">
              Редактировать
            </router-link>
            <button @click="deleteEmployee(employee.id)" class="btn btn-danger">Удалить</button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Пагинация -->
    <div class="pagination">
      <button
        @click="changePage(currentPage - 1)"
        :disabled="currentPage === 1"
        class="btn btn-secondary"
      >
        Предыдущая
      </button>
      <span>Страница {{ currentPage }} из {{ totalPages }}</span>
      <button
        @click="changePage(currentPage + 1)"
        :disabled="currentPage === totalPages"
        class="btn btn-secondary"
      >
        Следующая
      </button>
    </div>
  </div>
</template>

<script>
import { employeesApi } from '@/services/api'; // Предполагаем, что такой API будет

export default {
  data() {
    return {
      employees: [],
      currentPage: 1,
      limit: 10,
      total: 0,
      totalPages: 0,
    };
  },
  created() {
    this.fetchEmployees();
  },
  methods: {
    fetchEmployees() {
      employeesApi.getEmployees(this.currentPage, this.limit)
        .then(response => {
          console.log('Response from server:', response);
          if (!response.data || !response.data.meta) {
            throw new Error('Invalid response format from server');
          }
          this.employees = response.data.data;
          this.total = response.data.meta.total;
          this.totalPages = response.data.meta.totalPages;
        })
        .catch(error => {
          console.error('Ошибка при получении списка сотрудников:', error.message);
          alert('Произошла ошибка при загрузке данных. Попробуйте позже.');
        });
    },
    changePage(page) {
      if (page < 1 || page > this.totalPages) return;
      this.currentPage = page;
      this.fetchEmployees();
    },
    deleteEmployee(id) {
      if (!confirm('Вы уверены, что хотите удалить этого сотрудника?')) return;

      employeesApi.deleteEmployee(id)
        .then(() => {
          this.employees = this.employees.filter(emp => emp.id !== id);
          alert('Сотрудник успешно удален!');
        })
        .catch(error => {
          console.error('Ошибка при удалении сотрудника:', error);
          alert(`Произошла ошибка при удалении сотрудника: ${error.message}`);
        });
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg';
      }
    },
    formatDate(dateString) {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString();
    },
  },
};
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: center;
}
.table img {
  width: 50px;
  height: auto;
  object-fit: cover;
  border-radius: 5px;
}
.btn {
  margin-right: 5px;
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: 14px;
}
.pagination span {
  margin: 0 10px;
  font-size: 16px;
}
</style>