<template>
  <div class="furniture-employee-view">
    <h1>Просмотр сотрудника</h1>
    <div v-if="loading">Загрузка данных...</div>
    <div v-else-if="error">{{ error }}</div>
    <div v-else>
      <p><strong>ФИО:</strong> {{ employee.full_name }}</p>
      <p><strong>Должность:</strong> {{ employee.position }}</p>
      <p><strong>Телефон:</strong> {{ employee.phone }}</p>
      <p><strong>Email:</strong> {{ employee.email || "Не указан" }}</p>
      <p><strong>Дата найма:</strong> {{ formatDate(employee.hire_date) }}</p>
      <p><strong>Зарплата:</strong> {{ employee.salary }} ₽</p>
      <p><strong>Стаж работы:</strong> {{ employee.experience_years }} лет</p>
      <p><strong>Фото:</strong></p>
      <img
        :src="getAssetImage(employee.image_url)"
        alt="Фото сотрудника"
        v-if="employee.image_url"
        style="max-width: 200px; height: auto; border-radius: 8px;"
      />
      <span v-else>Нет фото</span>
    </div>
    <router-link to="/employees" class="btn btn-secondary">Назад к списку</router-link>
  </div>
</template>

<script>
import { employeesApi } from '@/services/api'; // Предполагаем, что такой API есть

export default {
  data() {
    return {
      employee: null,
      loading: true,
      error: null,
    };
  },
  async created() {
    await this.fetchEmployee();
  },
  methods: {
    async fetchEmployee() {
      try {
        const response = await employeesApi.getEmployeeById(this.$route.params.id);
        this.employee = response.data;
        this.loading = false;
      } catch (error) {
        this.error = 'Ошибка при загрузке данных о сотруднике';
        this.loading = false;
        console.error('Ошибка при загрузке сотрудника:', error);
      }
    },
    getAssetImage(imageName) {
      try {
        return require(`@/assets/${imageName}`);
      } catch (error) {
        console.warn(`Изображение не найдено: ${imageName}`);
        return '/path/to/default-image.jpg'; // Заглушка
      }
    },
    formatDate(dateString) {
      if (!dateString) return "Не указана";
      const date = new Date(dateString);
      return date.toLocaleDateString("ru-RU");
    },
  },
};
</script>

<style scoped>
.furniture-employee-view {
  margin: 20px auto;
  max-width: 600px;
}
img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}
.btn {
  margin-top: 20px;
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  display: inline-block;
}
.btn:hover {
  background-color: #0056b3;
}
</style>