<template>
  <div class="furniture-employee-edit">
    <h1>Редактировать сотрудника</h1>
    <form @submit.prevent="updateEmployee">
      <label for="full_name">ФИО:</label>
      <input type="text" id="full_name" v-model="formData.full_name" required />

      <label for="position">Должность:</label>
      <input type="text" id="position" v-model="formData.position" required />

      <label for="phone">Телефон:</label>
      <input type="text" id="phone" v-model="formData.phone" required />

      <label for="email">Email:</label>
      <input type="email" id="email" v-model="formData.email" />

      <label for="hire_date">Дата найма:</label>
      <input type="date" id="hire_date" v-model="formData.hire_date" required />

      <label for="salary">Зарплата (₽):</label>
      <input type="number" id="salary" v-model.number="formData.salary" required />

      <label for="experience_years">Стаж работы (лет):</label>
      <input type="number" id="experience_years" v-model.number="formData.experience_years" required />

      <label for="image_url">URL фото:</label>
      <input type="text" id="image_url" v-model="formData.image_url" />

      <button type="submit">Сохранить изменения</button>
    </form>
  </div>
</template>

<script>
import { employeesApi } from '@/services/api'; // Предполагаем, что такой API есть

export default {
  data() {
    return {
      formData: {
        full_name: '',
        position: '',
        phone: '',
        email: '',
        hire_date: '',
        salary: null,
        experience_years: null,
        image_url: '',
      },
    };
  },
  async created() {
    await this.fetchEmployee();
  },
  methods: {
    async fetchEmployee() {
      try {
        const response = await employeesApi.getEmployeeById(this.$route.params.id);
        this.formData = response.data;
      } catch (error) {
        console.error("Ошибка при загрузке данных сотрудника:", error);
        alert('Не удалось загрузить данные о сотруднике');
      }
    },
    async updateEmployee() {
      try {
        await employeesApi.updateEmployee(this.$route.params.id, this.formData);
        this.$router.push("/employees");
      } catch (error) {
        console.error("Ошибка при обновлении сотрудника:", error);
        alert('Произошла ошибка при сохранении данных');
      }
    },
  },
};
</script>

<style scoped>
.furniture-employee-edit {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
label {
  font-weight: bold;
}
input, textarea, select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
button {
  padding: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
button:hover {
  background-color: #218838;
}
</style>