<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header bg-dark text-white">
            <h3 class="text-center">Добро пожаловать, {{ username }}!</h3>
          </div>
          <div class="card-body">
            <p class="text-center">Вы успешно вошли в систему.</p>
            <button @click="logout" class="btn btn-danger w-100">Выйти</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: ''
    };
  },
  async created() {
    await this.checkAuth();
  },
  methods: {
    async checkAuth() {
      const response = await fetch('/protected');
      if (!response.ok) {
        this.$router.push('/login');
        return;
      }

      const data = await response.json();
      this.username = data.userId; // Предполагаем, что сервер возвращает userId
    },
    async logout() {
      await fetch('/logout', { method: 'GET' });
      this.$router.push('/login');
    }
  }
};
</script>