import { createRouter, createWebHistory } from 'vue-router';

// Главные страницы
import HomeView from '../views/HomeView.vue';
import AboutView from '../views/AboutView.vue';
import ContactsView from '../views/ContactsView.vue';

// Страницы для товаров мебельного цеха
import ProductsListView from '../views/ProductsListView.vue';
import ProductAddView from '../views/ProductAddView.vue';
import ProductEditView from '../views/ProductEditView.vue';
import ProductDetailView from '../views/ProductDetailView.vue';

// Страницы для сотрудников мебельного цеха
import EmployeesListView from '../views/EmployeesListView.vue';
import EmployeeAddView from '../views/EmployeeAddView.vue';
import EmployeeEditView from '../views/EmployeeEditView.vue';
import EmployeeDetailView from '../views/EmployeeDetailView.vue';

// Страницы авторизации
import UserLogin from '../components/UserLogin.vue';
import UserRegister from '../components/UserRegister.vue';
import AdminDashboard from '../components/AdminDashboard.vue';
import UnauthorizedView from '../views/UnauthorizedView.vue';

const routes = [
  // Основные страницы
  { path: '/', name: 'Home', component: HomeView },
  { path: '/about', name: 'About', component: AboutView },
  { path: '/contacts', name: 'Contacts', component: ContactsView },

  // Маршруты для товаров мебельного цеха
  {
    path: '/products',
    name: 'ProductsList',
    component: ProductsListView,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/products/add',
    name: 'ProductAdd',
    component: ProductAddView,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/products/:id/edit',
    name: 'ProductEdit',
    component: ProductEditView,
    meta: { requiresAuth: true, role: 'admin' },
    props: true
  },
  {
    path: '/products/:id',
    name: 'ProductView',
    component: ProductDetailView,
    meta: { requiresAuth: true, role: 'admin' },
    props: true
  },

  // Маршруты для сотрудников мебельного цеха
  {
    path: '/employees',
    name: 'EmployeesList',
    component: EmployeesListView,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/employees/add',
    name: 'EmployeeAdd',
    component: EmployeeAddView,
    meta: { requiresAuth: true, role: 'admin' }
  },
  {
    path: '/employees/:id/edit',
    name: 'EmployeeEdit',
    component: EmployeeEditView,
    meta: { requiresAuth: true, role: 'admin' },
    props: true
  },
  {
    path: '/employees/:id',
    name: 'EmployeeView',
    component: EmployeeDetailView,
    meta: { requiresAuth: true, role: 'admin' },
    props: true
  },

  // Авторизация
  { path: '/login', name: 'Login', component: UserLogin },
  { path: '/register', name: 'Register', component: UserRegister },
  { path: '/dashboard', name: 'Dashboard', component: AdminDashboard },
  { path: '/unauthorized', name: 'Unauthorized', component: UnauthorizedView },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

// Навигационный хук для защиты маршрутов
router.beforeEach(async (to, from, next) => {
  if (to.meta.requiresAuth) {
    try {
      const response = await fetch('/protected');
      if (!response.ok) {
        return next('/login'); // Перенаправляем на страницу входа
      }

      const data = await response.json();
      const userRole = data.role;

      if (to.meta.role && userRole !== to.meta.role) {
        return next('/unauthorized'); // Перенаправляем на страницу "Нет доступа"
      }

      next(); // Разрешаем доступ
    } catch (error) {
      console.error('Ошибка при проверке авторизации:', error);
      next('/login'); // Перенаправляем на страницу входа
    }
  } else {
    next(); // Разрешаем доступ к другим маршрутам
  }
});

export default router;