<template>
  <div class="furniture-product-edit">
    <h1>Редактировать мебельный товар</h1>
    <form @submit.prevent="updateProduct">
      <label for="name">Название:</label>
      <input type="text" id="name" v-model="formData.name" required />

      <label for="category">Категория:</label>
      <input type="text" id="category" v-model="formData.category" required />

      <label for="price">Цена (₽):</label>
      <input type="number" id="price" v-model.number="formData.price" required />

      <label for="description">Описание:</label>
      <textarea id="description" v-model="formData.description"></textarea>

      <label for="material">Материал:</label>
      <input type="text" id="material" v-model="formData.material" required />

      <label for="dimensions">Габариты (см):</label>
      <input type="text" id="dimensions" v-model="formData.dimensions" placeholder="например: 200x90x80" required />

      <label for="image_url">URL изображения:</label>
      <input type="text" id="image_url" v-model="formData.image_url" />

      <label for="in_stock">Наличие:</label>
      <select id="in_stock" v-model="formData.in_stock" required>
        <option :value="true" :selected="formData.in_stock === true">В наличии</option>
        <option :value="false" :selected="formData.in_stock === false">Нет в наличии</option>
      </select>

      <button type="submit">Сохранить изменения</button>
    </form>
  </div>
</template>

<script>
import { furnitureApi } from '@/services/api'; // Предполагаем, что такой API есть

export default {
  data() {
    return {
      formData: {
        name: '',
        category: '',
        price: null,
        description: '',
        material: '',
        dimensions: '',
        image_url: '',
        in_stock: true,
      },
    };
  },
  async created() {
    await this.fetchProduct();
  },
  methods: {
    async fetchProduct() {
      try {
        const response = await furnitureApi.getProductById(this.$route.params.id);
        this.formData = response.data;
      } catch (error) {
        console.error("Ошибка при загрузке продукта:", error);
        alert('Не удалось загрузить данные о продукте');
      }
    },
    async updateProduct() {
      try {
        await furnitureApi.updateProduct(this.$route.params.id, this.formData);
        this.$router.push("/products"); // Перенаправление на список товаров
      } catch (error) {
        console.error("Ошибка при обновлении продукта:", error);
        alert('Произошла ошибка при сохранении данных');
      }
    },
  },
};
</script>

<style scoped>
.furniture-product-edit {
  margin: 20px auto;
  max-width: 600px;
}
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
label {
  font-weight: bold;
}
input, textarea, select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
}
button {
  padding: 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
button:hover {
  background-color: #218838;
}
</style>